<template>
  <div>
    <Layout>
      <Header style="height:50px">
        <Menu mode="horizontal" theme="dark" style="height:50px">
          <div style="float:left;" class="fontsize">
            <p><font>PaddleTest</font></p>
          </div>
          <div style="float: right;">
            <MenuItem name="1">
              <div>
                <Dropdown
                  transfer
                  trigger="click"
                  v-on:on-click="handleClickApp"
                >
                  <a href="javascript:void(0)">
                    <span class="main-user-name">{{ appname }}</span>
                    <Icon type="md-arrow-dropdown"></Icon>
                  </a>
                  <DropdownMenu slot="list">
                    <DropdownItem name="changeApp"> 更换产品 </DropdownItem>
                  </DropdownMenu>
                </Dropdown>
              </div>
            </MenuItem>
            <MenuItem name="2" v-if="username">
              <div>
                <Dropdown
                  transfer
                  trigger="click"
                  v-on:on-click="handleClickUser"
                >
                  <a href="javascript:void(0)">
                    <img :src="avater" class="img-css">
                      <span>{{ username }}</span>
                    <img>
                    <Icon type="md-arrow-dropdown"></Icon>
                  </a>
                  <DropdownMenu slot="list">
                    <DropdownItem name="feedback"> 用户反馈 </DropdownItem>
                  </DropdownMenu>
                  <DropdownMenu slot="list">
                    <DropdownItem name="usertips"> 使用手册 </DropdownItem>
                  </DropdownMenu>
                  <DropdownMenu slot="list">
                    <DropdownItem name="logout"> 退出 </DropdownItem>
                  </DropdownMenu>
                </Dropdown>
              </div>
            </MenuItem>
          </div>
        </Menu>
      </Header>
      <Layout>
        <Sider
          :style="{
            overflow: 'auto',
            height: '100vh',
            position: 'sticky',
            top: 0,
            left: 0
          }"
          ref="side1"
          hide-trigger collapsible 
          :collapsed-width="70"
          v-model="isCollapsed"
        >
          <Menu
            theme="light"
            width="auto"
            style="height:100%;"
          >
            <div v-if="isCollapsed">
              <Card style="text-align:center;">
                <div class="item-css" @click="collapsedSider">
                  <right></right>
                </div>
              </Card>
              <Card style="text-align:center;">
                <div class="item-css">
                  <el-dropdown @command="handleClickTag">
                    <span class="el-dropdown-link">
                      <el-tooltip
                        effect="dark"
                        placement="top"
                        :content="option"
                      >
                        <Icon
                          size="18"
                          type="md-git-branch"
                        ><i class="el-icon-arrow-down el-icon--right"></i>
                        </Icon>
                      </el-tooltip>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item
                        v-for="(item, index) in verisonList"
                        :key="index"
                        :command="item"
                      >{{ item.desc }}
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
              </Card>
              <div class="item-css">
                <menu-fold :data="menuDesc" father-link="/paddle"></menu-fold>
              </div>
            </div>
            <div v-else>
              <Card style="text-align:center;">
                <div class="item-css" @click="collapsedSider">
                  <left></left>
                </div>
              </Card>
              <Card style="text-align:center;">
                <div class="item-css">
                  <el-dropdown @command="handleClickTag">
                    <span class="el-dropdown-link">
                      {{ option }}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item
                        v-for="(item, index) in verisonList"
                        :key="index"
                        :command="item"
                      >{{ item.desc }}
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
              </Card>
              <div class="item-css-new">
                <menu-nav :data="menuDesc" father-link="/paddle"></menu-nav>
              </div>
            </div>
            <!--
            <MenuItem name="1-2">
              <el-dropdown v-if="appid==1">
                <el-button>
                  {{ option }}
                </el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    :key="index"
                    @click.native="handleClickTag(item)"
                    v-for="(item, index) in verisonList"
                  >
                    {{ item.desc }}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </MenuItem>
            -->
          </Menu>
        </Sider>
        <Layout class="main-layout-content">
          <Content
            :style="{
                minHeight: currentPageMinHeight,
                maxHeight: currentPageMaxHeight,
                position: 'relative',
                background: '#fff',
                overflow: 'hidden'}"
            >
              <!--渲染视图-->
              <router-view ref="" class="view"></router-view>
            </Content>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import { MenuInfoUrl, LogoutUrl } from '../api/url.js';
import api from "../api/index";
import MenuNav from './CommonUtil/MenuNav.vue';
import MenuFold from './CommonUtil/MenuFold.vue';
import Left from './BaseIcon/Left.vue';
import Right from './BaseIcon/Right.vue';

export default {
  data: function () {
    return {
      isCollapsed: false,
      page: {
        page: 1,
        size: 50
      },
      username: Cookies.get('username'),
      avater: Cookies.get('avater'),
      appid: Cookies.get('appid'),
      appname: Cookies.get("appname"),
      menuDesc: {},
      verisonList: [],
      option: '',
      currentPageMinHeight: '861px',
      currentPageMaxHeight: ''
    };
  },
  computed: {
    BreadcrumbDescArr() {
      const pathItemArr = this.$route.path.split('/');
      let menuDict = Object.assign({}, this.menuDesc);
      let menuItemArr = [];
      for (var i = 0; i < pathItemArr.length; i++) {
        const pathItem = pathItemArr[i];
        if (pathItem && isNaN(pathItem) && menuDict.hasOwnProperty(pathItem)) {
          menuItemArr.push(menuDict[pathItem].desc);
          menuDict = menuDict[pathItem].sub;
        }
      }
      return menuItemArr;
    },
    // rotateIcon () {
    //   return [
    //     'menu-icon',
    //     this.isCollapsed ? 'rotate-icon' : ''
    //   ];
    // },
    // menuitemClasses () {
    //   return [
    //     '',
    //     this.isCollapsed ? 'collapsed-menu' : ''
    //   ]
    // },
    versionName: {
      get() {
        return this.$store.state.version;
      }
    }
  },
  components: {
    MenuNav,
    MenuFold,
    Left,
    Right
  },
  watch: {
    versionName: function () {
      this.option = this.versionName;
    }
  },
  mounted: async function () {
    this.appname = Cookies.get('appname');
    this.username = Cookies.get('username');
    this.avater = Cookies.get('avater');
    this.setUserInfo();
    await this.getMenu();
  },
  async created() {
    this.$root.Hub.$on('update-user-name', () => {
      this.appname = Cookies.get('appname');
      // this.username = Cookies.get('username');
    });
    await this.getMenu();
  },
  methods: {
    handleClickTag (item) {
      this.option = item.desc;
      this.$store.commit('changeVersion', this.option);
    },
    async handleClickUser(item) {
      // 清理cookie 跳转登出
      if (item === 'logout') {
        Cookies.remove('username');
        Cookies.remove('avater');
        Cookies.remove('userid');
        await api.get(LogoutUrl);
      } else if (item === 'feedback') {
        // 打开用户反馈页面 todo
      } else if (item === 'usertips') {
        // 跳转到用户手册知识库
        let href = 'https://ku.baidu-int.com/knowledge/HFVrC7hq1Q/t7n0qKWNJW/QMxJ7wiFu-/JlxaUkV3t989L0';
        window.open(href, '_blank');
      }
    },
    handleClickApp(name) {
      Cookies.remove('appid');
      Cookies.remove('appname');
      this.$store.commit('removeCurrentApp');
      this.$router.push({path: '/app_store'});
    },
    collapsedSider() {
      this.isCollapsed = !this.isCollapsed;
      this.$refs.side1.toggleCollapse();
    },
    setUserInfo() {
      this.$store.commit('changeUserName', this.username);
      this.$store.commit('changeAvater', this.avater);
    },
    async getMenu() {
      // 根据appid实时获取menu菜单; 各自定义各自的菜单
      let params = {
        appid: Cookies.get("appid"),
        appname: Cookies.get("appname")
      };
      const { code, data, message } = await api.get(MenuInfoUrl, params);
      if (parseInt(code, 10) === 200) {
        this.menuDesc = data;
        // 暂时定义menu
        this.verisonList = this.menuDesc.version;
        if (this.$route.params && this.$route.params.version) {
          this.option = this.$route.params.version.replace('-', "/");
          Cookies.set('version', this.option);
          Cookies.set('ver', this.option);
          this.$store.commit('changeVersion', this.option);
        } else if (this.$store.state.version) {
          this.option = this.$store.state.version;
          Cookies.set('version', this.option);
          Cookies.set('ver', this.option);
        } else {
          this.option = this.verisonList[1].desc;
          Cookies.set('version', this.option);
          Cookies.set('ver', this.option);
          this.$store.commit('changeVersion', this.option);
        }
        this.$delete(this.menuDesc, 'version');
      } else {
        this.$Message.error({
          content: '请求出错: ' + message,
          duration: 30,
          closable: true
        });
      }
    }
  }
}
</script>
<style scoped>
  .item-css-new {
    font-size: 14px;
    color: #303133;
    padding: 0 1px;
    cursor: pointer;
    -webkit-transition: border-color .3s,background-color .3s,color .3s;
    -o-transition: border-color .3s,background-color .3s,color .3s;
    transition: border-color .3s,background-color .3s,color .3s;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  .item-css {
    font-size: 14px;
    color: #303133;
    padding: 0 6px;
    cursor: pointer;
    -webkit-transition: border-color .3s,background-color .3s,color .3s;
    -o-transition: border-color .3s,background-color .3s,color .3s;
    transition: border-color .3s,background-color .3s,color .3s;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  .item-css >>> .ivu-icon-ios-arrow-down{
    display: none;
  }
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-header-bar{
    background: #fff;
  }
  .layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  .menu-icon{
    transition: all .3s;
  }
  .footer{
    right: 0;
    bottom: 0;
  }
  .rotate-icon{
    transform: rotate(-90deg);
  }
  .menu-item span{
    display: inline-block;
    overflow: hidden;
    width: 69px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: bottom;
    transition: width .2s ease .2s;
  }
  .fontsize {
    font-size: 22px;
    font-weight: bold;
    font-family: "楷体";
    /*background-image: linear-gradient(top, orange, purple);*/
    background-image: -webkit-linear-gradient(right, green, yellow, red);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .user-dropdown {
    &-menu-con {
      /*position: absolute;*/
      /*right: 0;*/
      /*top: 0;*/
      /*width: auto;*/
      /*height: 100%;*/
      .main-user-name {
          display: inline-block;
          word-break: keep-all;
          white-space: nowrap;
          vertical-align: middle;
          overflow: hidden;
          text-overflow: ellipsis;
          text-align: right;
          font-size: 12px;
      }
    }
    &-innercon {
        height: 100%;
        padding-right: 14px;
    }
}
  .menu-item i{
    transform: translateX(0px);
    transition: font-size .2s ease, transform .2s ease;
    vertical-align: middle;
    font-size: 16px;
  }
  .user-dropdown-menu-con {
    display: inline-block;
  }
  .main-layout-content {
    /*padding: 7px 9px 24px 9px;*/
    /*width: 80%;*/
    /*margin-top: 54px;*/
    /*margin-left: 198px;*/

    width: auto;
  }
  .img-css {
    display: inline-block;
    word-break: keep-all;
    white-space: nowrap;
    vertical-align: middle;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: right;
    width: 24px;
    height: 24px;
    border-radius: 24px;
  }
</style>


