<template>
    <div class="one-fifth-video-col">
        <div style="font-weight: bold; line-height:200%; margin-bottom: 1%">
        <Divider orientation="center">例行任务及基线参数</Divider>
            <Row>
                <Col span="4"> 例行任务(Latest)</Col>
                <Col span="4"> Place: {{ latest.place }}</Col>
                <Col span="6"> 执行时间：{{ latest.create_time }}</Col>
                <Col span="10"> Commit: {{ latest.commit }}</Col>
            </Row>
            <Row>
                <Col span="4"> 基线任务(Baseline)</Col>
                <Col span="6"> Version: {{ baseline.version }}</Col>
            </Row>
        <Divider orientation="center">API性能数据汇总</Divider>
        <Row>
            <Col span="4"></Col>
            <Col span="4">性能较好：{{ summary.good }}</Col>
            <Col span="6">性能接近：{{ summary.same }}</Col>
            <Col span="10">性能较差：{{ summary.bad }}</Col>
        </Row>

        </div>
        <div>
            <Table
                border
                :loading="loading"
                :columns="columns"
                :data="result"
            >
            </Table>
        </div>
    </div>
</template>

<script>
import api from '../../api/index';
import { ApiCompare } from '../../api/url.js';
export default {
    name: 'HomePage',
    data: function () {
        return {
            loading: true,
            result: [],
            baseline: {},
            latest: {},
            summary: {},
            columns: [
                {
                    title: 'API',
                    key: 'api',
                    width: 200,
                    fixed: 'left',
                    filter: {
                        type: 'input'
                    }
                },
                {
                    title: 'Casename',
                    key: 'case_name',
                    width: 150,
                    fixed: 'left'
                },
                {
                    title: '前向',
                    align: 'center',
                    children: [
                        {
                            title: 'Latest',
                            align: 'center',
                            width: 150,
                            className: 'demo-table-info-forward-column',
                            render: (h, params) => {
                                return h('div', [
                                    h('p', {}, parseFloat(params.row.latest.forward))
                                ]);
                            }
                        },
                        {
                            title: 'Baseline',
                            align: 'center',
                            width: 150,
                            className: 'demo-table-info-forward-column',
                            render: (h, params) => {
                                return h('div', [h('p', {}, params.row.baseline.forward)]);
                            }
                        },
                        {
                            title: '前向差距',
                            align: 'center',
                            width: 150,
                            sortable: true,
                            key: 'compare',
                            className: 'demo-table-info-forward-column',
                            sortMethod: function (a, b, type) {
                                if (type === 'asc') {
                                    return a.forward > b.forward ? 1 : -1;
                                } else {
                                    return a.forward > b.forward ? -1 : 1;
                                }
                            },
                            render: (h, params) => {
                                let value = parseFloat(params.row.compare.forward);
                                let str = this.round(value);
                                return h('div', [
                                    h('p', {
                                        style: {
                                            color: this.setValueColor(value)
                                        }
                                    }, str)
                                ]);
                            }
                        }
                    ]
                },
                {
                    title: '反向',
                    align: 'center',
                    children: [
                        {
                            title: 'Latest',
                            align: 'center',
                            width: 150,
                            className: 'demo-table-info-backward-column',
                            render: (h, params) => {
                                return h('div', [h('p', {}, params.row.latest.backward)]);
                            }
                        },
                        {
                            title: 'Baseline',
                            align: 'center',
                            width: 150,
                            className: 'demo-table-info-backward-column',
                            render: (h, params) => {
                                return h('div', [h('p', {}, params.row.baseline.backward)]);
                            }
                        },
                        {
                            title: '反向差距',
                            align: 'center',
                            width: 150,
                            sortable: true,
                            key: 'compare',
                            className: 'demo-table-info-backward-column',
                            sortMethod: function (a, b, type) {
                                if (type === 'asc') {
                                    return a.backward > b.backward ? 1 : -1;
                                } else {
                                    return a.backward > b.backward ? -1 : 1;
                                }
                            },
                            render: (h, params) => {
                                let value = parseFloat(params.row.compare.backward);
                                let str = this.round(value);
                                return h('div', [
                                    h('p', {
                                        style: {
                                            color: this.setValueColor(value)
                                        }
                                    }, str)
                                ]);
                            }
                        }
                    ]
                },
                {
                    title: '整体',
                    align: 'center',
                    children: [
                        {
                            title: 'Latest',
                            align: 'center',
                            width: 150,
                            className: 'demo-table-info-total-column',
                            render: (h, params) => {
                                return h('div', [h('p', {}, params.row.latest.total)]);
                            }
                        },
                        {
                            title: 'Baseline',
                            align: 'center',
                            width: 150,
                            className: 'demo-table-info-total-column',
                            render: (h, params) => {
                                return h('div', [h('p', {}, params.row.baseline.total)]);
                            }
                        },
                        {
                            title: '整体差距',
                            align: 'center',
                            width: 150,
                            sortable: true,
                            key: 'compare',
                            className: 'demo-table-info-total-column',
                            sortMethod: function (a, b, type) {
                                console.log(a.total, b.total, type);
                                if (type === 'asc') {
                                    return a.total > b.total ? 1 : -1;
                                } else {
                                    return a.total > b.total ? -1 : 1;
                                }
                            },
                            render: (h, params) => {
                                let value = parseFloat(params.row.compare.total);
                                let str = this.round(value);
                                return h('div', [
                                    h('p', {
                                        style: {
                                            color: this.setValueColor(value)
                                        }
                                    }, str)
                                ]);
                            }
                        }
                    ]
                }
            ]
        };
    },
    mounted: function () {
        this.getCompareData();
    },
    methods: {
        round(value) {
            return value.toFixed(4).toString() + 'x';
        },
        setValueColor(value) {
            if (value >= 1.15) {
                return 'green';
            } else if (value < -1.15) {
                return 'red';
            } else {
                return '';
            }
        },
        async getCompareData() {
            const { code, data, message } = await api.post(ApiCompare);
            if (parseInt(code, 10) === 200) {
                this.result = data[0].compare;
                this.baseline = data[0].baseline;
                this.latest = data[0].latest;
                this.summary = data[0].summary;
                this.loading = false;
            } else {
                this.$Message.error({
                    content: '请求出错: ' + message,
                    duration: 30,
                    closable: true
                });
            }
        }
    }
};
</script>

<style>
.one-fifth-video-col {
    margin-right: 1%;
    margin-left: 1%;
    margin-bottom: 1%;
    margin-top: 0.5%;
    font-size: 14px;
}
.ivu-table td.demo-table-info-forward-column {
    background-color: #fffeff;
}
.ivu-table td.demo-table-info-backward-column {
    background-color: #faffff;
}
.ivu-table td.demo-table-info-total-column {
    background-color: #fffff8;
}
</style>



